<template>
	<div>
		<!-- <div id="map"></div> -->
		<div id="container"></div>
	</div>
</template>

<script>
	export default {
		name:"lhrMap",
		mounted() {
			console.log(1);
			var map = new BMapGL.Map('container'); // Map
			map.centerAndZoom(new BMapGL.Point(121.498205,31.384404), 15); // ,
			map.enableScrollWheelZoom(true); // 
			var marker1 = new BMapGL.Marker(new BMapGL.Point(121.498205,31.384404));
			// 
			map.addOverlay(marker1);
		}
	}
</script>

<style scoped>
	#container {

		overflow: hidden;
		width: 100%;
		height: 500px;
		margin: 0;
		font-family: "";

	}
</style>
